import { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { fetchLoggedInUserOrdersAsync, selectUserInfoStatus, selectUserOrders} from "../userSlice";
import { InfinitySpin } from "react-loader-spinner";

export default function UserOrders() {
  const dispatch = useDispatch();
  const orders = useSelector(selectUserOrders);
  const status = useSelector(selectUserInfoStatus);

  useEffect(() => {
    dispatch(fetchLoggedInUserOrdersAsync());
  }, [dispatch]);

  return (
    <>
      {status === "loading" && <InfinitySpin width="200" color="#00BFFF" />}
      {orders && orders.map((order, index) => (
          <div key={index} className="mx-auto mt-2 bg-white max-w-5xl px-4 sm:px-6 lg:px-8">
            <div className="border-t border-gray-200 px-4 py-2 sm:px-10">
              <h1 className="text-2xl font-bold tracking-tight text-gray-900">
                Order id # {order.id}
              </h1>
              <h3 className="text-1xl font-bold tracking-tight text-red-900">
                Order Status : {order.status}
              </h3>
              <div className="flow-root">
                <ul role="list" className="-my-5 divide-y divide-gray-200">
                  {order.items.map((item) => (
                    <li key={item.id} className="flex py-6">
                      <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                        <img
                          src={item.product.thumbnail}
                          alt={item.product.title}
                          className="h-full w-full object-cover object-center"
                        />
                      </div>
                      <div className="ml-4 flex flex-1 flex-col">
                        <div>
                          <div className="flex justify-between text-base font-medium text-gray-900">
                            <h3>
                              <a href={item.id}>{item.product.title}</a>
                            </h3>
                            <p className="ml-4">{item.product.discountPrice}</p>
                          </div>
                          <p className="mt-1 text-sm text-gray-500">
                            {item.product.brand}
                          </p>
                        </div>
                        <div className="flex flex-1 items-end justify-between text-sm">
                          <div className="text-gray-500">
                            <label
                              htmlFor="email"
                              className="inline mr-3 text-sm font-medium leading-6 text-gray-900"
                            >
                              Qty :{item.quantity}
                            </label>
                          </div>
                        </div>
                      </div>
                    </li>
                  ))}
                </ul>
              </div>
            </div>
            <div className="border-t py-2 border-gray-200 px-4 sm:px-10">
              <div className="flex mt-1 justify-between text-base font-medium text-gray-900">
                <p>Total items in Cart</p>
                <p>{order.totalItems} items</p>
              </div>
              <div className="flex justify-between text-base font-medium text-gray-900">
                <p>Subtotal</p>
                <p>${order.totalAmount}</p>
              </div>
              <p className="mt-0.5 text-sm text-gray-500">Shipping Address :</p>
              <div className="flex justify-between gap-x-6 mt-1 px-3 rounded border-solid border-2 border-gray-200 ">
                <div className="flex min-w-0 gap-x-4">
                  <div className="min-w-0 flex-auto">
                    <p className="text-sm font-semibold leading-6 text-gray-900">
                      {order.selectedAddress.name}
                    </p>
                    <p className="mt-1 truncate text-xs leading-5 text-gray-500">
                      {order.selectedAddress.email}
                    </p>
                    <p className="mt-1 truncate text-xs leading-5 text-gray-500">
                      {order.selectedAddress.street}
                    </p>
                    <p className="mt-1 truncate text-xs leading-5 text-gray-500">
                      {order.selectedAddress.pinCode}
                    </p>
                  </div>
                </div>
                <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                  <p className="text-sm leading-6 text-gray-900">
                    Phone : {order.selectedAddress.phone}
                  </p>
                  <p className="text-sm leading-6 text-gray-900">
                    {order.selectedAddress.state}
                  </p>
                  <p className="text-sm leading-6 text-gray-900">
                    {order.selectedAddress.city}
                  </p>
                </div>
              </div>
            </div>
          </div>
        ))}
    </>
  );
}
